header{
  /* 
  부모 요소의 기준으로 절대값 위치 지정 
  그러나 부모 요소에 position 속성이 없는 경우, 루트 요소를 부모로 생각한다!
  */
  position: fixed; 
  top: 0;
  left: 0;
  background-color: #f0eff0;
  width: 33%;
  height: 100%;
  /*
  4개의 값 : 위 오른 아래 왼
  !important : 속성값이 겹쳐서 안 먹히는 경우에, 반드시 이것에 우선순위를 둔다!
  */
  padding: 10vh 0 72px 72px !important;
  display: flex !important;
  flex-direction: column;
  justify-content: space-between;
  color: #e7473c;
}
header > h1{
  font-size: 75px;
  width: 50%;
  letter-spacing: 5px;
}

.bottom > h3{
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 40px;
}
.bottom > p{
  font-weight: 300;
  font-size: 18px;
  margin-bottom: 40px;
}
.bottom > h3, .bottom > p{
  width: 70%;
  line-height: 1.2;
  text-align: justify;
}
.bottom > nav > ul{
  width: 80%;
  display: flex;
  flex-wrap: wrap;
}
.bottom > nav > ul > li{
  cursor: pointer;
  margin-right: 12px;
  font-size: 20px;
  color: #000;
  opacity: .5;
  border-bottom: 1px solid #000;
  margin-bottom: 20px;
}
.bottom > nav > ul > li:hover{
  opacity: 1;
}

main{
  background-color: #e7473c;
  margin-left: 33%;
}
main > .movie{
  height: 100vh;
  padding: 12% 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}
main > .movie > .wrapper{
  width: 100%;
  height: 100%;
  display: flex;
  /*
  display : flex의 기본 주축이 row!
  */
  flex-direction: column;
}
main > .movie > .wrapper > .movie__header{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 25px;
}
main > .movie > .wrapper > .movie__header > h4{
  font-size: 28px;
}
main > .movie > .wrapper > .movie__header > h5{
  font-size: 18px;
  letter-spacing: 2px;
}
main > .movie > .wrapper > .movie__video,
main > .movie > .wrapper > .movie__video > iframe{
  width: 100%;
  height: 100%;
}

footer{
  align-items: center;
  /*
  그라데이션 주기 어떤색부터 어떤 색까지 서서히...?
  */
  background-image: linear-gradient(#e6e6e6,#fff);
  /*
  inset : 오프셋을 정의하지 않는다.
  */
  box-shadow: inset 0 5px 12px rgba(0, 0, 0, .3);
  display: flex !important;
  justify-content: center;
  /*
  부모요소를 따라가며 고정된다.
  */
  position: sticky;
  height: 120px;
  padding: 0 144px !important;
  /*
  calc(내부 연산의 결과를 속성값으로 적용한다.)
  */
  top: calc(100vh - 120px);
}
footer > p{
  color: #e7473c;
  font-size: 24px;
}
footer > p > strong{
  font-weight: 700;
}